.brochure {
    background: url(../img/Brouchur.jpg);
    background-size: cover;

    &__content {
        padding-block: 3rem;
    }

    &__img {
        max-width: 100%;
    }

    &__wrapper {
        padding-block: 48px;
        text-align: center;
    }

    &__title {
        font-size: 2rem;
        font-weight: 500;
        line-height: 1.2;
        margin-bottom: 0.5rem;
        color: white;
    }

    &__desc {
        font-size: 1.25rem;
        font-weight: 300;
        line-height: 1.5;
        margin-bottom: 1rem;
        color: white;
    }

    &__link {
        font-size: 1.5rem;
        text-decoration: underline;
        color: #5b81ff;
    }
}